:root {
    --primary: #A84fff;
    --secondary: #d81e5b;
    --dark: #131a26;
    --light: #eeeeee;
    --grey: #888888;

    --font: 'Fira Sans', sans-serif;
    --font-quote: 'Fira Code', monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--font);
}

body {
    background-color: var(--light);
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

.quote {
    position: relative;
}

blockquote {
    font-family: var(--font-quote);
    max-width: 720px;
    font-size: 2rem;
    background-color: var(--dark);
    padding: 3rem;
    border-radius: 1.5rem;
    color: var(--light);
}

.title,
.id {
    display: inline-block;
    padding: 0.75rem 1.5rem;
    color: var(--light);
    border-radius: 1.5rem;
    font-weight: 700;
    font-size: 1.4rem;
    letter-spacing: 1px;
    max-width: 360px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.title {
    background-color: var(--secondary);
    font-weight: 400;
    transform: translate(-25%, 50%);

}

.id {
    background-color: var(--primary);
    font-weight: 700;
    transform: translate(25%, 50%);
    position: absolute;
    right: 0;
    bottom: 0;
}

button {
    appearance: none;
    border: none;
    outline: none;

    background-color: var(--primary);
    color: var(--light);
    border-radius: 3rem;
    padding: 0.75rem 1.5rem;
    margin-top: 64px;
    margin-bottom: 64px;
    font-size: 1.5rem;
    font-weight: 700;

    transition: 0.4s;
    cursor: pointer;
}

button:hover {
    background-color: var(--secondary);
}